.fn-mb-20 {
  margin-bottom: 20px;
}
.fn-mb-40 {
  margin-bottom: 20px;
}
.updatepage {
  padding: 50px 0 0 0;
  text-align: center;
}
.updatepage .logo {
  width: 482px;
  margin: 0 auto;
  padding: 0;
}
.updatepage .logo li {
  float: left;
  width: 240px;
  height: 90px;
  text-align: center;
  padding: 10px 0 0 0;
  cursor: pointer;
  list-style: none;
}
.updatepage .logo li.ie {
  border-right: 1px solid #e9e9e9;
}
.updatepage .logo li a {
  color: #666;
  text-decoration: none;
}
.updatepage .logo li a:hover {
  color: #4492d4;
}
